<template>
  <div>
    <h2>Radios</h2>
    <cdr-radio id="test1" name="example" value="a1" v-model="ex1">A1</cdr-radio>
    <cdr-radio name="example" value="a2" v-model="ex1">A2</cdr-radio>
    <cdr-radio name="example" :value="{val:'a3'}" v-model="ex1">A3</cdr-radio>
    <cdr-radio name="example" value="a4" v-model="ex1" disabled>A4</cdr-radio>
    <p>Group A Picked: {{ex1}}</p>

    <cdr-radio name="example2" value="b1" v-model="ex2">B1</cdr-radio>
    <cdr-radio name="example2" value="b2" v-model="ex2">B2</cdr-radio>
    <p>Group B Picked: {{ex2}}</p>
  </div>
</template>

<script>
import * as Components from '../../_index';

export default {
  name: 'radios',
  components: Components,
  data() {
    return {
      ex1: '',
      ex2: 'b2',
    };
  },
};
</script>

<style lang="css">
</style>



